<!-- TODO:  JSX  -->
<template>
    <div class="attr-list">
        <el-form>
            <el-form-item v-for="(key, index) in styleKeys.filter(item => item != 'rotate')" :key="index" :label="map[key]">
                <el-color-picker v-if="key == 'borderColor'" v-model="curComponent.style[key]"></el-color-picker>
                <el-color-picker v-else-if="key == 'color'" v-model="curComponent.style[key]"></el-color-picker>
                <el-color-picker v-else-if="key == 'backgroundColor'" v-model="curComponent.style[key]"></el-color-picker>
                <el-select v-else-if="selectKey.includes(key)" v-model="curComponent.style[key]">
                    <template v-if="key == 'textAlign'">
                        <el-option
                            v-for="item in textAlignOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </template>
                    <template v-else-if="key == 'borderStyle'">
                        <el-option
                            v-for="item in borderStyleOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </template>
                    <template v-else>
                        <el-option
                            v-for="item in verticalAlignOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </template>
                </el-select>
                <el-input type="number" v-else v-model="curComponent.style[key]" />
            </el-form-item>
            <el-form-item label="" v-if="curComponent && !excludes.includes(curComponent.component)">
                <el-input type="textarea" v-model="curComponent.propValue" />
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            excludes: ['Picture', 'Group'], // 
            textAlignOptions: [
                {
                    label: '',
                    value: 'left',
                },
                {
                    label: '',
                    value: 'center',
                },
                {
                    label: '',
                    value: 'right',
                },
            ],
            borderStyleOptions: [
                {
                    label: '',
                    value: 'solid',
                },
                {
                    label: '',
                    value: 'dashed',
                },
            ],
            verticalAlignOptions: [
                {
                    label: '',
                    value: 'top',
                },
                {
                    label: '',
                    value: 'middle',
                },
                {
                    label: '',
                    value: 'bottom',
                },
            ],
            selectKey: ['textAlign', 'borderStyle', 'verticalAlign'],
            map: {
                left: 'x ',
                top: 'y ',
                height: '',
                width: '',
                color: '',
                backgroundColor: '',
                borderStyle: '',
                borderWidth: '',
                borderColor: '',
                borderRadius: '',
                fontSize: '',
                fontWeight: '',
                lineHeight: '',
                letterSpacing: '',
                opacity: '',
                textAlign: '',
                verticalAlign: '',
            },
        }
    },
    computed: {
        styleKeys() {
            return this.$store.state.curComponent ? Object.keys(this.$store.state.curComponent.style) : []
        },
        curComponent() {
            return this.$store.state.curComponent
        },
    },
}
</script>

<style lang="scss" scoped>
.attr-list {
    overflow: auto;
    padding: 20px;
    padding-top: 0;
    height: 100%;
}
</style>
